import React, { Component, lazy, Suspense } from "react";
import { BrowserRouter, Route, Switch } from 'react-router-dom';
import { withRouter } from 'react-router';
import NavBar from './components/NavBar';
import NotFound from './components/NotFound';
import './App.css';
import Login from "./login/Login";
import withAuth from './login/withAuth';
import Rpio from "./rpio/RpioBoard";
import Sensor from "./sensors/Sensor";
import Room from "./sensors/Room";
import { createCookieOneHour } from "./login/CookieHandler";
//import ErrorBoundary from "./components/ErrorBoundary";

const Hub = withRouter(
  lazy(() => import('./hub/Hub'))
);

const Settings = withRouter(
  lazy(() => import('./hub/Settings'))
);

const Sensors = withRouter(
  lazy(() => import('./sensors/Sensors'))
);

const Rooms = withRouter(
  lazy(() => import('./sensors/Rooms'))
);

const urlout = window.location.protocol + "//" + window.location.host + "/api/logout";

class App extends Component {
  constructor(props) {
    super(props);

    this.state = {
        isLogdIn: localStorage.getItem("isLogdIn"),
        token: ''
    };
    this.handleLoginToken = this.handleLoginToken.bind(this);
    this.handleLogout = this.handleLogout.bind(this);
  }

  handleLogout = () => {
    fetch(urlout, {
        method: 'GET',
        credentials: 'include', // <--- YOU NEED THIS LINE
        redirect: "follow"
    })
    .then(res => {
        if (res.status === 200) {
            console.log(res, res.headers);
            this.setState({
                token: '',
                isLogdIn: false
            })
            localStorage.setItem("isLogdIn", false);
        }
    })
    .catch(err => {
        console.log(err);
    });
  }

  handleLoginToken = (val) => {
      //console.log(val);
      this.setState({
          token: val,
          isLogdIn: true
      })
      //console.log(this.state);
      createCookieOneHour(val);
  }

  render() {

    return (
      <BrowserRouter>
      <div className="allwidth">
          <NavBar
            isLogdin={this.state.isLogdIn}
            hlo={this.handleLogout} />
            <Suspense fallback={<div>Laddar...</div>}>
              <Switch>
                <Route exact path={"/"} component={withAuth(Hub)} />
                <Route exact path={"/rooms"} component={withAuth(Rooms)} />
                <Route path={"/room/:id"} component={withAuth(Room)} />
                <Route exact path={"/sensors"} component={withAuth(Sensors)} />
                <Route path={"/sensor/:id"} component={withAuth(Sensor)} />
                <Route path={"/settings"} component={withAuth(Settings)} />
                <Route path={"/rpio"} component={withAuth(Rpio)} />
                <Route path={"/login"} render={(props)=><Login {...props} handleLoginToken={this.handleLoginToken}/>}/>

                <Route exact path="**" component={NotFound} />
              </Switch>
            </Suspense>
      </div>
      </BrowserRouter>
    );
  }
}

export default App;
